module.exports = [[module.id, "/* You can add global styles to this file, and also import other style files */\n:root {\n  --primary-color: black;\n  --secondary-color: rgba(229,148,0,0.9);\n  --overlay-color: rgba(46, 62, 209, 0.85);\n  --menu-speed: 0.75s;\n  /* All this variables are available on menu.css as well \n      because style.css is included 1st in index.html   */\n}\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\nbody {\n  font-family: \"Roboto\", sans-serif;\n  line-height: 1.4;\n}\n.container {\n  max-width: 960px;\n  margin: auto;\n  overflow: hidden;\n  padding: 0 3rem;\n}\n#showcase {\n  background: var(--primary-color);\n  color: #fff;\n  height: 100vh;\n  position: relative;\n}\n#showcase::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: -1;\n}\n#showcase .showcase-content {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  flex-direction: column;\n  height: 100%;\n}\n#showcase h1 {\n  font-size: 4rem;\n}\n#showcase p {\n  font-size: 1.3rem;\n}\n.btn {\n  display: inline-block;\n  border: none;\n  background: var(--primary-color);\n  color: #fff;\n  padding: 0.75rem 1.5rem;\n  margin-top: 1rem;\n  transition: opacity 1s ease-out;\n  text-decoration: none;\n}\n.btn:hover {\n  opacity: 0.4;\n}\n.menu-wrap {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 1;\n}\n.menu-wrap .toggler {\n  position: absolute;\n  width: 50px;\n  height: 50px;\n  /* background: red; */\n  opacity: 0;\n  top: 0;\n  left: 0;\n  z-index: 2;\n  cursor: pointer;\n}\n.menu-wrap .hamburger {\n  position: absolute;\n  top: 0;\n  left: 0;\n  padding: 1rem;\n  background: var(--primary-color);\n  width: 60px;\n  height: 60px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 1;\n}\n/* Hamburger Lines */\n.menu-wrap .hamburger > div {\n  position: relative;\n  flex: none;\n  width: 100%;\n  height: 2px;\n  background: #fff;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.4s ease-in-out;\n}\n/* Hamburger Line Before & After  */\n.menu-wrap .hamburger > div:before,\n.menu-wrap .hamburger > div:after {\n  content: \"\";\n  position: absolute;\n  top: -10px;\n  z-index: 1;\n  width: 100%;\n  height: 2px;\n  background: inherit;\n}\n.menu-wrap .hamburger > div::after {\n  top: 10px;\n}\n/* Toggler Animation */\n/* .menu-wrap .toggler:checked + .menu-wrap .hamburger > div ------->  Problem  */\n.menu-wrap .toggler:checked + .hamburger > div {\n  transform: rotate(135deg);\n}\n.menu-wrap .toggler:checked + .hamburger > div:before,\n.menu-wrap .toggler:checked + .hamburger > div:after {\n  top: 0;\n  transform: rotate(90deg);\n}\n.menu-wrap .toggler:checked:hover + .hamburger > div {\n  transform: rotate(225deg);\n}\n/* Menu-Overlay (hidden) */\n.menu-wrap .menu {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  visibility: hidden;\n  overflow: hidden;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.menu-wrap .menu > div {\n  background: var(--overlay-color);\n  width: 200vw;\n  height: 200vh;\n  border-radius: 50%;\n  display: flex;\n  flex: none;\n  align-items: center;\n  justify-content: center;\n  transform: scale(0);\n  transition: all 0.4s ease;\n}\n.menu-wrap .menu > div > div {\n  text-align: center;\n  max-width: 90vw;\n  max-height: 100vh;\n  opacity: 0;\n  /* background: red; */\n  transition: opacity 0.4s ease;\n}\n.menu-wrap .menu ul li {\n  list-style: none;\n  color: #fff;\n  font-size: 1.5rem;\n  padding: 1rem;\n}\n.menu-wrap .menu ul li a {\n  color: inherit;\n  text-decoration: none;\n  transition: color 0.7s ease;\n}\n.menu-wrap .menu ul li a:hover {\n  color: var(--secondary-color);\n}\n/* Show Menu */\n.menu-wrap .toggler:checked ~ .menu > div {\n  transform: scale(1);\n  transition-duration: var(--menu-speed);\n}\n.menu-wrap .toggler:checked ~ .menu > div > div {\n  opacity: 1;\n  transition: opacity 0.4s ease;\n}\n.menu-wrap .toggler:checked ~ .menu {\n  visibility: visible;\n}\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]